<template>
  <div>
      <col-title>用户注册</col-title>
      		<div class="wrapbox padding-footer">
			<div id="item1" class="withdraw-operate">
				<div class="padding20">
					<table class="table-default">
						<tr>
							<th>用户名：</th>
							<td><input type="text" placeholder="请输入用户姓名" v-model="userName"></td>
						</tr>
						<tr>
							<th>密码：</th>
							<td><input type="password" placeholder="请输入密码" v-model="passWord"></td>
						</tr>
						<tr>
							<th>确认密码：</th>
							<td><input type="password" placeholder="请再次输入密码" v-model="rePassWord"></td>
						</tr>
                        <tr>
							<th>性别：</th>
							<td>
								<input type="radio" id="radio-1-1" name="radio-1" value="男" class="regular-checkbox" v-model="sex"/><label for="radio-1-1"><i></i>男士</label>
								<input type="radio" id="radio-1-2" name="radio-1" value="女" class="regular-checkbox"  checked="checked" v-model="sex"/><label for="radio-1-2"><i></i>女士</label>
							</td>
						</tr>
						<tr>
							<th>手机号码：</th>
							<td><input type="tel" placeholder="请输入手机号码" v-model="tel"></td>
						</tr>
						<tr>
                            <th>获取验证码：</th>
							<td><mt-button type="primary" v-model="telCode" size="small" @click="getTelCode" :disabled="isDisabled">点击发送验证码<em v-if="isDisabled">({{secNum}}秒)</em></mt-button></td>
						</tr>
                        <tr>
                            <th>填写验证码：</th>
							<td><input type="text" placeholder="请输入验证码" v-model="telCode"></td>
						</tr>
					</table>					
				</div>
				<div class="box-space-top15">
					<div class="sys-service padding20">
						<h1 class="txt-assistant">温馨提示</h1>
						<p>以上信息用于下单，请认真填写并仔细核对，以免造成不必要的财产损失哦！</p>
					</div>
				</div>
			</div>
		</div>
		<footer class="footer-bar">
			<div class="padding20-x">
				<button class="btn-lg btn-ok" @click="subReg">确认提交</button>
			</div>
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
	export default {
		name:'register',
		components: {
			colTitle
		},
		data () {
			return {
				userName:'',
				passWord:'',
				rePassWord:'',
				sex:'女',
				tel:'',
				telCode:'',
				isDisabled:false,
				secNum:30
			}
		},
		methods: {
			subReg(){
				// 表单验证
				if (this.userName=='') {
					Toast({
						message: '用户名不能为空',
						position: 'top',
						duration: 2000
					});
					return false
				}
				if (this.passWord=='') {
					Toast({
						message: '密码不能为空',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					if (this.passWord!=this.rePassWord) {
					Toast({
						message: '两次密码不一致',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				if (this.tel=='') {
					Toast({
						message: '手机号不能为空',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					let pattern = /^1[34578]\d{9}$/;
					if (!pattern.test(this.tel)) {
					Toast({
						message: '手机号格式不正确',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				if (this.telCode=='') {
					Toast({
						message: '短信验证码不能为空',
						position: 'top',
						duration: 2000
					});
					return false
				}
				this.$http.post('/User/Register',{
					"name": this.userName,
					"pwd":  this.passWord,
					"tel": 	this.tel,
					"sex": 	this.sex,
					"code": this.telCode
				})
				.then(res=>{
					console.log(res)
					Toast({
						message: '注册成功！',
						position: 'center',
						duration: 3000
					})
					this.userName = '',
					this.passWord = '',
					this.rePassWord = '',
					this.sex = '女',
					this.tel = '',
					this.telCode = ''
					this.$router.push('/login')
				})
				.catch(err=>{
					console.log(err)
				})
			},
			// 获取短信验证码
			getTelCode(){
				if (this.tel=='') {					// 获取短信验证码时验证手机号码
					Toast({
						message: '手机号不能为空',
						position: 'top',
						duration: 2000
					});
					return false
				}else{
					let pattern = /^1[34578]\d{9}$/;
					if (!pattern.test(this.tel)) {
					Toast({
						message: '手机号格式不正确',
						position: 'top',
						duration: 2000
					});
					return false
					}
				}
				this.isDisabled = true;
				let	timer1=setInterval(()=>{
						this.secNum>=0?this.secNum--:this.secNum=0
					},1000)
					setTimeout(() => {
						this.isDisabled = false
						clearTimeout(timer1)
						this.secNum = 30
					}, 30000);
				this.$http.post('/User/Sms',{
					tel:this.tel
				})
				.then(res=>{
					console.log(res)
					this.telCode = res.data.code
					Toast({
						message: '你的短信验证码为：'+this.telCode,
						position: 'center',
						duration: 3000
					});
				})
			}
		}
	}
</script>
<style scoped>
.table-default th{text-align: right;padding-right: 15px}
.table-default td em{font-style: normal}
</style>



