<template>
  <div>
      <col-title>{{data.beautician_name}}</col-title>
      		<div class="wrapbox">
			<!--头像展示区-->
			<div class="avatar-box avatar-zj">
				<div class="imgbox">
					<!--无头像时调用的默认系统头像-->
					<img :src="data.image_url?imgUrl+data.image_url:no_img">
				
					<i class="mark mark-renzheng" v-if="data.is_verify">已认证</i>
				</div>
				<div class="user-info-show">
					<h2>{{data.beautician_name}} {{data.duties}}</h2>
					<p>擅长项目关键词/分类</p>
				</div>
			</div>
			<div class="padding20-x">
				<h3 class="title-col"><b>医师介绍</b><a class="pull-right">展开/收起</a></h3>
				<div class="expert-desc packup">{{data.intro}}</div>
			</div>
			<div class="box-space-top15">
				<div class="padding20-x"><h3 class="title-col"><b>风采展示</b></h3></div>
				<ul class="expert-picshow">
					<li v-for="item in data.image_list" ><img :src="imgUrl+item.image_url"></li>
				</ul>
			</div>
			<ul class="list list-jg">
				<li>
					<h2 class="title" @click="goShopDetail(data.shop_id)">{{data.shop_name}}</h2>
					<p class="qualification">资质：{{data.shop_aptitude}}</p>
					<p class="address">地址：{{data.shop_address}}</p>					
					<div class="btnbox"><button class="btn-icon btn-tel">电话咨询</button><button class="btn-icon btn-favorate" @click="saveFavor()">加入收藏</button></div>
					<div class="imgbox"  @click="goShopDetail(data.shop_id)"><img :src="data.shop_logo?imgUrl+data.shop_logo:no_img"><i class="mark mark-renzheng" v-if="data.shop_is_verify">已认证</i></div>
				</li>
			</ul>
			<div class="padding20-x"><h3 class="title-col"><b>擅长项目</b></h3></div>
			<ul class="list list-xm">
				<li v-for="item in goods_list" @click="goGoodsDetail(item.id)">
					<h2 class="title">{{item.goods_name}}</h2>
					<p class="desc">{{item.intro}}</p>
					<p class="price"><span class="price-now">{{item.old_price.toFixed(2)}}</span><span class="price-old">{{item.price.toFixed(2)}}</span></p>
					<div class="tagbox"><i class="tag tag-tehui" v-if="item.is_buy_now">特惠</i><i class="tag tag-baoxian" v-if="item.is_special_offer">推荐</i></div>
					<div class="imgbox"><img :src="imgUrl+item.image_url"></div>
					<button class="btn-yuyue" >详 情</button>
				</li>
			</ul>			
		</div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
export default {
  name:'beautician_detail',
  components: {
      colTitle
  },
  data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			beautician_id:this.$route.query.id,
			data:[],
			goods_list:[]
		}
	},
	mounted(){
		this.getMainData()
	},
	methods: {
		getMainData(){
			this.$http.get('/Beautician/'+this.beautician_id).then(res=>{
				this.data = res.data;
				this.data.image_list = JSON.parse(this.data.image_list);
			})
			.catch(err=>{
				this.$router.push('/error')
			})

			this.$http.get('/Goods?beautician_id='+this.beautician_id+'&index=1&size=20').then(res=>{
				this.goods_list = res.data;
			})
			.catch(err=>{
				this.$router.push('/error')
			})
		},
		goGoodsDetail(id){
			this.$router.push('/goods_detail?id='+id);
		},
		goShopDetail(id){
			this.$router.push('/shop_detail?id='+id);
		},
		saveFavor()
		{
			if(!this.tokenId)
			{
				Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'bottom',
						duration: 2000
				});
				return ;
			}

			this.$http.post('/UserFavor',{
					type: 2,
					itemid: this.data.shop_id
			},{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{
				if(res.data.status=="saved")
				{
					Toast({
							message: '收藏成功',
							position: 'bottom',
							duration: 2000
					});
				}
				else
				{
					Toast({
							message: '已取消收藏',
							position: 'bottom',
							duration: 2000
					});
				}
			})
			.catch(err=>{
				var that = this;
				if(err.response.status==401)
				{
					Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'bottom',
						duration: 2000
					});

					setTimeout(function(){
						localStorage.setItem("return_url","/beautician_detail?id="+that.beautician_id);
						that.$router.push('/Login')
					},2000)
				}
				else
				{
					this.$router.push('/error');
				}
			})
			
		}
	}
}
</script>



