<template>
    <div>
        <col-title>修改用户头像</col-title>
        <div class="wrapbox">
            <div class="user_face">
                <a class="btn_addPic" href="javascript:void(0);">
                    <span><img :src="upImg" /></span>
                    <input class="filePrew" type="file" @change="modifyPic">
                </a>
            </div>
            <div class="padding20-x">
                <button class="btn-lg btn-ok" @click="submitImg">提交修改</button>
            </div>
	    </div>
    </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
export default {
    name:'userFace',
    components: {
        colTitle
    },
    data(){
        return{
            upImg:this.$route.query.face_img,
            tokenId:localStorage.getItem("tokenId")
        }
    },
    mounted(){
        if(this.tokenId)
        {
            //this.getData();
        } 
		else
			this.goLogin();
    },
    methods: {
        modifyPic(e){
            new html5ImgCompress(e.target.files[0], {
                before: function (file) {
                },
                done:(file, base64)=>{
                    this.upImg = base64
                }
            });
        },
        fn(){
            let e = document.createEvent("MouseEvents")
                e.initEvent("change", true, true);
                this. modifyPic(e)
        },
        submitImg(){
            this.$http.post('/User',{
				image_data:this.upImg,
            },{headers: {"authorization": "Bearer " + this.tokenId}})
            .then(res=>{
                Toast({
                    message: '头像修改成功！',
                    position: 'center',
                    duration: 2000
                });
                setTimeout(() => {
                    this.$router.push('/user')
                }, 2000);
            
            }).catch(err=>{
                if(err.response.status==401)
                    this.goLogin();
                else
                    this.$router.push('/error');
			})
        },
        goLogin()
        {
            localStorage.setItem("return_url","/user_face");
            this.$router.push('/Login')
        }
    }
}
</script>
<style scoped>
.user_face{padding: 30px;text-align: center;margin: 10% auto}
.btn_addPic{ display: inline-block; position: relative;width: 240px;height:240px; cursor: pointer;overflow: hidden;}
.btn_addPic img{width: 240px;height: 240px;border-radius:50%;border: 4px solid #efefef} 
.btn_addPic .tips-img{width: 30px;height: 30px;position: absolute;right: 0px;top:0px;z-index: 8888;}
.btn_addPic span{display: block;position: relative;} 
.filePrew { display: block; position: absolute; top: 0; left:0; width: 240px;height:240px; opacity: 0;filter:alpha(opacity=0);}
</style>



