<template>
  <div>
      <col-title>{{data.goods_name}}</col-title>
      		<div class="wrapbox padding-footer">
			<!--幻灯片-->
			<carousel :bannerList='this.data.banner_list'></carousel>
			<div class="xm-title padding20">
				<h1>{{data.intro}}</h1>
				<p>
					<!-- <span class="pull-right place">所在地</span> -->
					<i class="tag tag-baoxian" v-if="data.is_buy_now">特惠</i><i class="tag tag-tehui" v-if="data.is_special_offer">推荐</i></p>
			</div>
			<div class="box-space-top15">
				<div class="xm-pricebox padding20"><span class="pull-right tip-yuyue">已预约：{{data.sale_count}}</span><span class="price-now">¥{{data.price}}</span><span class="price"> (¥{{data.old_price}})</span><i class="tag-zhekou">{{(data.price/data.old_price).toFixed(2)}}折</i></div>
				<div class="xm-dikoubox clearfix">
					<span>积分返现 <strong>{{data.score}}</strong></span>				
				</div>
			</div>
			
			<!--机构信息-->
			<ul class="list list-jg">
				<li>
					<h2 class="title" @click="goShopDetail(data.shop_id)">{{data.shop_name}}</h2>
					<p class="qualification">资质：{{data.shop_aptitude}}</p>
					<p class="address">地址：{{data.shop_address}}</p>					
					<div class="btnbox"><button class="btn-icon btn-tel">电话咨询</button><button class="btn-icon btn-favorate" @click="saveFavor(2,data.shop_id)">加入收藏</button></div>
					<div class="imgbox" @click="goShopDetail(data.shop_id)"><img :src="data.shop_logo?imgUrl+data.shop_logo:no_img"><i class="mark mark-renzheng" v-if="data.shop_is_verify">已认证</i></div>
				</li>
			</ul>
			<div class="edit-box padding20" v-html="data.description"></div>		
		</div>
		<footer class="footer-bar footer-xm">
			<div class="price pull-left">
				<h3>现价：<strong>￥{{data.price}}</strong></h3>
				<p>原价：￥{{data.old_price}}</p>
			</div>
			<span class="btn-yuyue pull-right"><button @click="goOrderBuy()">立即预约</button></span>
			<span class="btn-shoucang pull-right" @click="saveFavor(1,goods_id)"><button>收藏</button></span>			
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import Carousel from '../common/carousel'
import { Toast } from 'mint-ui'
export default {
  name:'detail',
  components: {
	  colTitle,
	  Carousel
  },
  data () {
		return {
			data:[],
			goods_id:this.$route.query.id,
			tokenId:localStorage.getItem("tokenId")
		}
	},
	mounted(){
		this.getMainData()
	},
	methods: {
		loadBottom(){
			console.log('sdf');
		},
		getMainData(){
			this.$http.get('/Goods/'+this.goods_id).then(res=>{
				this.data = res.data;
				this.data.banner_list = JSON.parse(this.data.banner_list);
			})
			.catch(err=>{
				this.$router.push('/error')
			})
		},
		goOrderBuy(){
			this.$router.push('/order_buy?id='+this.goods_id)
		},
		goShopDetail(id){
			this.$router.push('/shop_detail?id='+id);
		},
		saveFavor(typeId,itemId)
		{
			if(!this.tokenId)
			{
				Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'bottom',
						duration: 2000
				});
				return ;
			}

			this.$http.post('/UserFavor',{
					type: typeId,
					itemid: itemId
			},{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{

				if(res.data.status=="saved")
				{
					Toast({
							message: '收藏成功',
							position: 'bottom',
							duration: 2000
					});
				}
				else
				{
					Toast({
							message: '已取消收藏',
							position: 'bottom',
							duration: 2000
					});
				}
			})
			.catch(err=>{
				if(err.response.status==401)
				{
					Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'bottom',
						duration: 2000
					});
				}
				else
				{
					this.$router.push('/error');
				}
			})
			
		}
	}
}
</script>



