<template>
  <div>
      <col-title>我的收藏</col-title>
              <div class="wrapbox">
			<div class="tab-box">
				<a class="tab-item" v-for="(tab,index) in tabTitle" :class="tab.isActive?'active':''" @click="fun(index)">{{tab.name}}</a>
			</div>
            <div class="page-tab-container">  
                <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>  
                    <mt-tab-container-item id="tab1">  
                        <!-- cell组件 --> 
                    <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore"> 
                    <mt-cell>
                        <!-- 收藏项目 -->
                        <div id="item1">
                        <!--无记录时调用的box-->
                        <div class="withdraw-no" v-if="projectList.length==0">
                            <h1><a>您还没有收藏，马上去逛逛</a></h1>
                        </div>
                        <!--有记录-->
                        <ul class="list list-xm">
                            <li v-for="item in projectList" @click="goGoodsDetail(item.goods_id)">
                                <h2 class="title">{{item.goods_name}}</h2>
                                <p class="desc">{{item.intro}}</p>
                                <p class="price"><span class="price-now">{{item.price}}.00</span><span class="price-old">{{item.old_price}}.00</span></p>
                                <div class="tagbox"><i class="tag tag-tehui" v-if="item.is_buy_now">特惠</i><i class="tag tag-baoxian" v-if="item.is_special_offer">推荐</i></div>
                                <div class="imgbox"><img :src="imgUrl+item.image_url"><!-- <i class="mark mark-guanfang">官方</i> --></div>
                                <button class="btn-yuyue">查 看</button>
                            </li>
                        </ul>
                    </div>
                    </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore> 
                    </mt-tab-container-item>  
                    <mt-tab-container-item id="tab2">  
                        <!-- cell组件 -->
                        <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore">  
                        <mt-cell>
                            <!-- 收藏机构 -->
                            <div id="item2">
                            <!--无记录时调用的box-->
                            <div class="withdraw-no" v-if="orgList.length==0">
                                <h1><a>您还没有收藏，马上去逛逛</a></h1>
                            </div>
                            <!--有记录-->
                            <ul class="list list-jg">
                                <li v-for="item in orgList" @click="goShopDetail(item.shop_id)">
                                    <h2 class="title">{{item.shop_name}}</h2>
                                    <p class="qualification">资质：{{item.aptitude}}</p>
                                    <p class="address">地址：{{item.address}}</p>
                                    <div class="countbox">
                                        <p class="total">已预约：<b class="txt-important">{{item.sale_count}}</b></p>		
                                    </div>
                                    <div class="imgbox"><img :src="imgUrl+item.logo"><i class="mark mark-renzheng">已认证</i></div>
                                </li>
                            </ul>
                        </div>
                        </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore>  
                    </mt-tab-container-item>
                </mt-tab-container>     
            </div>  
        </div>
    </div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
    name:'favorite',
    components: {
      colTitle
    },
    data () {
      return {
          tabTitle:[
              {name:"收藏项目",id:"1",isActive:true},
              {name:"收藏机构",id:"2",isActive:false},
          ],
          tokenId:localStorage.getItem("tokenId"),
          active: 'tab1',
          allLoaded: false,     //数据是否加载完毕
          bottomStatus: '',     //底部上拉加载状态
          pageSize:5,           //每页大小初始
          pageIndex:1,          //页面索引
          projectList:[],       //项目列表
          orgList:[]            //机构列表
      }
    },
    watch: {
        active: function (val) {
            for (let n = 0; n < this.tabTitle.length; n++) {
                this.tabTitle[n].isActive = false;
            }
            var index = parseInt(val.replace("tab",""))-1
            this.tabTitle[index].isActive = true;
            this.active = val;
            console.log()
        }
    },
  mounted(){
        if(this.tokenId)
            this.getData()
		else
			this.goLogin();
  },
  methods:{
		getData(){
			this.$http.all([
				this.$http.get('/UserFavor?type=1&index='+this.pageIndex+'&size='+this.pageSize,{headers: {"authorization": "Bearer " + this.tokenId}}),
				this.$http.get('/UserFavor?type=2&index='+this.pageIndex+'&size='+this.pageSize,{headers: {"authorization": "Bearer " + this.tokenId}})
			])
			.then(this.$http.spread((proRes,orgRes)=>{
				this.projectList = proRes.data.data
				this.orgList = orgRes.data.data
			}))
			.catch(err=>{
                if(err.response.status==401)
                    this.goLogin();
                else
                    this.$router.push('/error');
			})
        },
        loadBottom(){
          this.pageIndex++;
          this.getData()
        },
        fun(n){
            for (let n = 0; n < this.tabTitle.length; n++) {
                this.tabTitle[n].isActive = false;
            }
            this.tabTitle[n].isActive = true;
            this.active = 'tab'+(n+1)+''
            if(n==0)
                this.orderType ="";
            else
                this.orderType = n
            this.getData()
        },
        goShopDetail(id) {
            this.$router.push('/shop_detail?id='+id);
        },
        goGoodsDetail(id) {
            this.$router.push('/goods_detail?id='+id);
        },
		goLogin()
		{
			localStorage.setItem("return_url","/favorite");
            this.$router.push('/Login')
		}
    }
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>


