<template>
    <div>
        <col-title>支付成功</col-title>
        <header class="header-bar">
            <a class="header-icon header-icon-back pull-left" href='back.aspx?vcSerialNo='></a>
            <a class="header-title">支付成功</a>
            <a class="header-icon header-icon-more pull-right"></a>
        </header>
        <div class="wrapbox">
        <!--确认支付-->
        <div class="pay_success">
            <h1>支付成功</h1>
        </div>
        <div class="padding20-x">
            <button class="btn-lg btn-ok" @click="goBack()">完 成</button>
        </div>
        </div>
    </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
    name:'successPay',
    components: {
	  colTitle
  },
  methods: {
      goBack()
      {
          this.$router.push('/order_list')
      }
  }
}
</script>
<style scoped>
.pay_confirm, .pay_success{
	background: url(../../assets/img/order-confirm.svg) no-repeat center top 0.5rem / 2rem;
	text-align: center;
	padding-top: 2.8rem;
	color: #333333;
	margin-bottom: 1rem;
}
.pay_confirm p, .pay_success p {
	font-size: 0.375rem;
}
.pay_confirm h1, .pay_success h1{
	font-size: 1rem;
	margin-top: 0.5rem;
}
.pay_confirm h1::before, .pay_success p b::before{
	content: "￥";
}
</style>
<style scoped>
.pay_success{
	background: url(../../assets/img/hook-selected.svg) no-repeat center top 0.5rem / 2rem;
}
.pay_success h1{
	font-size: 0.7rem;
	margin-bottom: 0.5rem;
}
</style>



