<template>
  <div>
      <col-title>选择美容师</col-title>
		<div class="wrapbox padding-footer">
        <!-- 上拉加载 -->
            <mt-loadmore       
            :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
            :auto-fill="false" ref="loadmore">
			<ul class="list list-zj list-choose-zj">
				<li v-for="item in data" :class="item.is_valid?'':'selected'" @click="Selected(item)">
					<h2 class="title">{{item.beautician_name}} <span class="txt-gray">{{item.duties}}</span></h2>
					<p class="intro">{{item.intro}}</p>
					<p class="serviceinfo"><span class="txt-i">{{item.keyword}}</span></p>
					<div class="imgbox"><img :src="imgUrl+item.image_url"><i class="mark mark-renzheng" v-if="item.is_verify">已认证</i></div>
				</li>
			</ul>			
			<div slot="bottom" class="mint-loadmore-bottom">
				<span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
				</div>
			</mt-loadmore>
		</div>
		<footer class="footer-bar"  @click="choosed">
			<div class="padding20-x">
				<button class="btn-lg btn-ok">确 定</button>
			</div>
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
	name:'order',
	components: {
		colTitle
	},
	data () {
		return {
			data:[],
			tokenId:localStorage.getItem("tokenId"),
			allLoaded: false,   //数据是否加载完毕
			bottomStatus: '',   //底部上拉加载状态
			goods_id:0,
			beautician:{}
		}
	},
	mounted(){
		this.$on('showBeautician',(val)=>{
			this.goods_id = val;
			this.getData();
		});
	},
	methods: {
		getData()
		{
			this.$http.get('/Beautician?goods_id='+this.goods_id+'&index=1&size=20',
			{
				headers: {"authorization": "Bearer " + this.tokenId}
			}).then(res=>{
				this.data = res.data.data;
			})
			.catch(err=>{
				this.$router.push('/Login')
			})
		}
		,
		loadBottom(){
			this.getData();
		},
		choosed(){
			//触发父窗口事件关闭美容师选择窗口，并传递选中美容师信息
			this.$emit('hideBeautician',this.beautician.id,this.beautician.beautician_name);
		},
		Selected(item)
		{
			//清除已有选项状态
			this.data.forEach(element => {
				element.is_valid = true;
			});
			//借用is_valid属性，设置位false表示显示选中状态
			item.is_valid = false;
			//设置选中的美容师信息
			this.beautician = item;
		}
	}
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>


