<template>
  <div>
      <col-title>订单列表</col-title>
        <div class="wrapbox">
			<div class="tab-box">
				<a class="tab-item" v-for="(tab,index) in tabTitle" :class="tab.isActive?'active':''" @click="fun(index)">{{tab.name}}</a>
			</div>
            <div class="page-tab-container">  
                <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>  
                    <mt-tab-container-item id="tab1">  
                        <!-- cell组件 --> 
                    <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore"> 
                    <mt-cell>
                    <!--项目-->
                    <ul class="list user-list-xm">
                        <li v-for="item in allOrderList">
                            <div class="company ziying" @click="goShopDetail(item.shop_id)">{{item.shop_name}}</div>
                            <div class="xm-info" @click="goOrderDetail(item.serial_no)">
                                <h2 class="title">{{item.goods_name}}</h2>
                                <p>操作时间：<span>{{item.start_time | subTime}}</span></p>
                                <p>操作技师：<span>{{item.beautician_name}}</span></p>
                                <p>订单编号：<span>{{item.serial_no}}</span></p>						
                                <div class="imgbox"><img :src="imgUrl+item.image_url"></div>
                            </div>
                            <p class="price">
                                <span class="total">数量：<b class="txt-i"> {{item.quantity}} </b></span>
                                总额：<b class="txt-i"><span class="price-now">{{item.total.toFixed(2)}}</span></b>
                            </p>
                            <div class="btnbox"><span class="txt-assistant">{{item.status_name}}</span>
                            <button class="btn-cancel" v-if="item.status_id == 1 || item.status_id == 2" @click="cancelOrder(item)">取消订单</button>
                            <button class="btn-zhifu" v-if="item.status_id == 1" @click="goComfirmOrder(item.serial_no)">去预约</button>
                            <button class="btn-zhifu" v-if="item.status_id == 2" @click="finlishOrder(item)">完成预约</button>
                            <button class="btn-zhifu" v-if="item.status_id == 3 || item.status_id == 4"  @click="goGoodsDetail(item.goods_id)">重新购买</button>
                            </div>			
                        </li>
                    </ul>		
                    </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore> 
                    </mt-tab-container-item>  
                    <mt-tab-container-item id="tab2">  
                        <!-- cell组件 -->
                        <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore">  
                        <mt-cell>
                            <ul class="list user-list-xm">
                                <li v-for="item in allOrderList">
                                    <div class="company ziying" @click="goShopDetail(item.shop_id)">{{item.shop_name}}</div>
                                    <div class="xm-info" @click="goOrderDetail(item.serial_no)">
                                        <h2 class="title">{{item.goods_name}}</h2>
                                        <p>操作时间：<span>{{item.start_time | subTime}}</span></p>
                                        <p>操作技师：<span>{{item.beautician_name}}</span></p>
                                        <p>订单编号：<span>{{item.serial_no}}</span></p>						
                                        <div class="imgbox"><img :src="imgUrl+item.image_url"></div>
                                    </div>
                                    <p class="price">
                                        <span class="total">数量：<b class="txt-i"> {{item.quantity}} </b></span>
                                        <b>总额：<span class="price-now">{{item.total.toFixed(2)}}</span></b>
                                    </p>
                                    <div class="btnbox"><span class="txt-assistant">{{item.status_name}}</span>
                                    <button class="btn-cancel" @click="cancelOrder(item)">取消订单</button>
                                    <button class="btn-zhifu" @click="goComfirmOrder(item.serial_no)">去预约</button></div>			
                                </li>
                            </ul>
                        </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore>  
                    </mt-tab-container-item>  
                    <mt-tab-container-item id="tab3">  
                        <!-- cell组件 -->  
                        <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore">  
                        <mt-cell>
                            <ul class="list user-list-xm">
                                <li v-for="item in allOrderList">
                                    <div class="company ziying" @click="goShopDetail(item.shop_id)">{{item.shop_name}}</div>
                                    <div class="xm-info" @click="goOrderDetail(item.serial_no)"> 
                                        <h2 class="title">{{item.goods_name}}</h2>
                                        <p>操作时间：<span>{{item.start_time | subTime}}</span></p>
                                        <p>操作技师：<span>{{item.beautician_name}}</span></p>
                                        <p>订单编号：<span>{{item.serial_no}}</span></p>						
                                        <div class="imgbox"><img :src="imgUrl+item.image_url"></div>
                                    </div>
                                    <p class="price">
                                        <span class="total">数量：<b class="txt-i"> {{item.quantity}} </b></span>
                                        <b>总额：<span class="price-now">{{item.total.toFixed(2)}}</span></b>
                                    </p>
                                    <div class="btnbox"><span class="txt-assistant">{{item.status_name}}</span>
                                    <button class="btn-cancel" @click="cancelOrder(item)">取消订单</button>
                                    <button class="btn-zhifu" @click="finlishOrder(item)">完成预约</button></div>			
                                </li>
                            </ul>
                        </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore>  
                    </mt-tab-container-item>
                    <mt-tab-container-item id="tab4">  
                        <!-- cell组件 -->  
                        <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore">  
                        <mt-cell>
                            <ul class="list user-list-xm">
                                <li v-for="item in allOrderList">
                                    <div class="company ziying" @click="goShopDetail(item.shop_id)">{{item.shop_name}}</div>
                                    <div class="xm-info" @click="goOrderDetail(item.serial_no)">
                                        <h2 class="title">{{item.goods_name}}</h2>
                                        <p>操作时间：<span>{{item.start_time | subTime}}</span></p>
                                        <p>操作技师：<span>{{item.beautician_name}}</span></p>
                                        <p>订单编号：<span>{{item.serial_no}}</span></p>						
                                        <div class="imgbox"><img :src="imgUrl+item.image_url"></div>
                                    </div>
                                    <p class="price">
                                        <span class="total">数量：<b class="txt-i"> {{item.quantity}} </b></span>
                                        <b>总额：<span class="price-now">{{item.total.toFixed(2)}}</span></b>
                                    </p>
                                    <div class="btnbox"><span class="txt-assistant">{{item.status_name}}</span>
                                    <!-- <button class="btn-cancel">取消订单</button> -->
                                    <button class="btn-zhifu" @click="goGoodsDetail(item.goods_id)">重新购买</button></div>			
                                </li>
                            </ul>
                        </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore>  
                    </mt-tab-container-item>
                    <mt-tab-container-item id="tab5">  
                        <!-- cell组件 -->  
                         <mt-loadmore       
                        :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                        :auto-fill="false" ref="loadmore">  
                        <mt-cell>
                            <ul class="list user-list-xm">
                                <li v-for="item in allOrderList">
                                    <div class="company ziying" @click="goShopDetail(item.shop_id)">{{item.shop_name}}</div>
                                    <div class="xm-info" @click="goOrderDetail(item.serial_no)">
                                        <h2 class="title">{{item.goods_name}}</h2>
                                        <p>操作时间：<span>{{item.start_time | subTime}}</span></p>
                                        <p>操作技师：<span>{{item.beautician_name}}</span></p>
                                        <p>订单编号：<span>{{item.serial_no}}</span></p>						
                                        <div class="imgbox"><img :src="imgUrl+item.image_url"></div>
                                    </div>
                                    <p class="price">
                                        <span class="total">数量：<b class="txt-i"> {{item.quantity}} </b></span>
                                        <b>总额：<span class="price-now">{{item.total.toFixed(2)}}</span></b>
                                    </p>
                                    <div class="btnbox"><span class="txt-assistant">{{item.status_name}}</span>
                                    <!-- <button class="btn-cancel">取消订单</button> -->
                                    <button class="btn-zhifu"  @click="goGoodsDetail(item.goods_id)">重新购买</button></div>			
                                </li>
                            </ul>
                        </mt-cell>
                    <div slot="bottom" class="mint-loadmore-bottom">
                        <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
                    </div>
                    </mt-loadmore>  
                    </mt-tab-container-item>  
                </mt-tab-container>  
            </div>  
        </div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { MessageBox } from 'mint-ui'
import { Toast } from 'mint-ui'
import { Indicator } from 'mint-ui'
export default {
    name:'order_list',
    components: {
        colTitle
    },
    data () {
        return {
            tabTitle:[
                {name:"全部",id:"",isActive:true},
                {name:"待付款",id:"1",isActive:false},
                {name:"已预约",id:"2",isActive:false},
                {name:"已完成",id:"3",isActive:false},
                {name:"已取消",id:"4",isActive:false}
            ],
            tokenId:localStorage.getItem("tokenId"),
            active: 'tab1',
            allLoaded: false,     //数据是否加载完毕
            bottomStatus: '',     //底部上拉加载状态
            allOrderList:[],
            pageSize:5,
            pageIndex:1,
            orderType:""
        }
    },
    watch: {
            active: function (val) {
                var index = parseInt(val.replace("tab",""))-1
                 this.fun(index);
                // for (let n = 0; n < this.tabTitle.length; n++) {
                //     this.tabTitle[n].isActive = false;
                // }
                // var index = parseInt(val.replace("tab",""))-1
                // this.tabTitle[index].isActive = true;
                // this.active = val;
            }
        },
        mounted(){
            if(this.tokenId)
            {
                //Indicator.open('加载中...')
                if(this.$route.query.typeId)
                    this.fun(this.$route.query.typeId);
                else
                    this.getData()
            }
            else
                this.goLogin();
         

        },
    methods:{
        getData(){
            this.$http.get("/Order?status="+this.orderType+"&index="+this.pageIndex+"&size="+this.pageSize,
            {headers: {"authorization": "Bearer " + this.tokenId}})
            .then(res=>{
                this.allOrderList = res.data.data                
            })
            .catch(err=>{
                if(err.response.status==401)
                    this.goLogin();
                else
                    this.$router.push('/error');
            })
        },
        loadBottom(){
            this.pageIndex++;
            this.getData()
        },
        fun(n){
            for (let n = 0; n < this.tabTitle.length; n++) {
                this.tabTitle[n].isActive = false;
            }
            this.tabTitle[n].isActive = true;
            this.active = 'tab'+(n+1)+''
            if(n==0)
                this.orderType ="";
            else
                this.orderType = n
            this.getData()
        },		
        goShopDetail(id){
			this.$router.push('/shop_detail?id='+id);
        },
        goOrderDetail(id){
			this.$router.push('/order_detail?id='+id);
        },
        goComfirmOrder(id)
		{
			//去订单支付页面
        	this.$router.push({path: 'order_buy', query:{serial_no: id}})
			console.log('去订单支付页面');
		},
        finlishOrder(item){
            MessageBox.confirm("确定执行此操作吗?").then(action => {
                if (action) {              
                    //更新列表项  取消订单
                    this.$http.post('/OrderComfirm',{
                        serial_no:item.serial_no,
                        status:3
                    },{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
                        Toast({
                            message: '完成预约成功，您的款项将发送至商家！',
                            position: 'center',
                            duration: 5000
                        });
                        item.status_id=3;
						item.status_name = '已完成';
                    })
                    .catch(err=>{
                        Toast({
                            message: '完成预约失败',
                            position: 'center',
                            duration: 5000
                        });
                    })
                }
            }).catch(cancel=>{
                console.log("bbb")
            })
        },
        cancelOrder(item){
             MessageBox.confirm("您确定要取消此订单吗?").then(action => {
                if (action) {
					//更新列表项  取消订单
                    this.$http.post('/OrderComfirm',{
                        serial_no:item.serial_no,
                        status:4
                    },{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
                        Toast({
                            message: '订单已取消',
                            position: 'center',
                            duration: 5000
                        });
                        item.status_id=4;
						item.status_name = '已取消';
                    })
                    .catch(err=>{
                        Toast({
                            message: '订单取消失败',
                            position: 'center',
                            duration: 5000
                        });
                    })
                }
            });
        },
        goGoodsDetail(id){
			this.$router.push('/goods_detail?id='+id);
        },
        goLogin()
        {
            localStorage.setItem("return_url","/order_list");
            this.$router.push('/Login')
        }
    }
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>


