<template>
  <div>
      <search-title>推荐技师/医师</search-title>
		<div class="wrapbox">
			
			<div class="box-space-top15">
				<!-- 上拉加载 -->
				<mt-loadmore       
				:bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
				:auto-fill="false" ref="loadmore">
				<ul class="list list-zj">
					<li v-for="item in beauticianList" @click="go_beautician_detail(item.id)">
						<h2 class="title">{{item.beautician_name}} <span class="txt-gray">{{item.duties}}</span></h2>
						<p class="workplace">所属医疗机构名称</p>
						<div class="imgbox"><img :src="imgUrl+item.image_url"><i class="mark mark-renzheng" v-if="item.is_verify">已认证</i></div>
					</li>
				</ul>
				<div slot="bottom" class="mint-loadmore-bottom">
					<span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
				</div>
				</mt-loadmore>
			</div>
		</div>
  </div>
</template>
<script>
import searchTitle from '../common/searchTitle'
import { Toast } from 'mint-ui';
export default {
   name:'beautician_list',
   components:{
      searchTitle
    },
    data () {
        return {
			query:'',
			pageIndex:0,
            pageSize:5,
			allLoaded: false,   //数据是否加载完毕
            bottomStatus: '',   //底部上拉加载状态
            beauticianList:[]
        }
    },
    mounted(){
		if(this.$route.query.keyWords)
			this.query = this.$route.query.keyWords;
		this.loadData()
    },
    methods: {
        loadData(){
			this.pageIndex++
            this.$http.get('/Beautician?order=1&index='+this.pageIndex+'&size='+this.pageSize+'&query='+this.query)
            .then(res=>{
				res.data.data.forEach(element => {
					this.beauticianList.push(element)
				});

                if (res.data.data.length==0) {
                    this.allLoaded = true				// 停止上拉
                    Toast({message:'没有更多内容了！',position: 'bottom',duration: 3000})
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
        loadBottom(){
			this.loadData();
		},
		go_beautician_detail(id){
			this.$router.push('/beautician_detail?id='+id);
		}
    }
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>


