<template>
  <div>
      <col-title>搜索</col-title>
      		<div class="wrapbox">
			<div class="search-bar">
				<div class="search-selected">
                  <select v-model="selected" @change="changeSelect">
                    <option v-for="item in items" :value="item.typeId">{{item.typeName}}</option>
                </select>
				</div>
				<input type="text" class="search-bar-input" placeholder="搜索项目 / 机构 / 技师"
                v-model="searchWords"
                @keyup.enter="searchResult"
                >
			</div>
			<div class="padding20">
				<h2 class="title-icon title-search-hot margin-bom20">大家都在搜</h2>
				<div class="keyword">
                    <a 
                    :class="'keyword' + words.type_id" 
                    v-for="words in KeysWordsArr"
                    @click="goResult(words)"
                    >{{words.search_key}}</a>
				</div>
			</div>
			<div class="box-space-top15">
				<div class="padding20">
					<h2 class="title-icon title-search-history" @click="clearKeysWords">历史记录<a class="pull-right"></a></h2>
					<ul class="list-singleline list-rightarrow list-search-history">
						<li 
                        v-for="words in localKeysWords"
                        @click="goResult(words)"
                        >{{words.search_key}}</li>
					</ul>
					<div class="system-info" v-show="localKeysWords.length<1">暂时还没有记录哦~</div>
				</div>
			</div>
		</div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
  name:'detail',
  components: {
      colTitle
  },
  data () {
    return {
    items:[{typeName:'项目',typeId:1},{typeName:'美容院',typeId:2},{typeName:'美容师',typeId:3}],
    selected:1,
    allSearchKeys:[],
    KeysWordsArr:[],
    searchWords:'',
    localKeysWords:[]
    }
  },
  mounted () {
      // 获取所有类型搜索关键词
      this.getSearchAllKeys()
      //加载
      this.loadKeyWords();
  },
  methods: {
    //加载localStorage中关键词
    getSearchAllKeys(){
          this.$http.get('/SearchKeys')
          .then(res=>{
              this.allSearchKeys = res.data;
              this.changeSelect();
          })
          .catch(err=>{
              this.$router.push('/error')
          })
    },
    //更改选择
    changeSelect(){
        var list = [];
        this.allSearchKeys.forEach(element => {
            if(element.type_id==this.selected)
                list.push(element);
        });
        this.KeysWordsArr = list;
    },
    //查询
    searchResult(e){
        if (e.keyCode === 13) {
            var obj = {"type_id":this.selected,"search_key":this.searchWords}
            this.saveKeyWords(obj);
            //跳转
            this.goResult(obj);
        }
    },
    // 清除历史记录
    clearKeysWords(){
        this.localKeysWords = []
        localStorage.removeItem('wordTemp')
    },
    // 历史记录点击跳转搜索
    goResult(item){
        console.log(JSON.stringify(item));
        if(item.type_id==1)
            this.$router.push({path: 'goods_list', query:{keyWords: item.search_key}})
        else if(item.type_id==2)
            this.$router.push({path: 'shop_list', query:{keyWords: item.search_key}})
        else if(item.type_id==3)
            this.$router.push({path: 'beautician_list', query:{keyWords: item.search_key}}) 
    },
    //保存关键词到localStorage
    saveKeyWords(obj){
        for (let n = 0; n < this.localKeysWords.length; n++) {
            if (this.localKeysWords[n].search_key == obj.search_key ) {
                this.localKeysWords.splice(n,1)
            }
        }
        this.localKeysWords.unshift(obj);
        localStorage.setItem('wordTemp',JSON.stringify(this.localKeysWords))
    },
    //加载localStorage中关键词
    loadKeyWords()
    {
        // 判断本地存储是否为空
        if (localStorage.getItem('wordTemp')==null) 
            return ;
        
        this.localKeysWords = JSON.parse(localStorage.getItem('wordTemp'))
    }
  }
}
</script>
<style scoped>
.search-selected select{background:#F7F7F7;margin-left: 20px;margin-top: 15px;font-size: 14px;-moz-appearance:none;-webkit-appearance:none;padding-left:5px}
.list-rightarrow li:first-child{width: 100%}
</style>



