<template>
  <div>
      <search-title>{{pageTitle}}</search-title>
      	<div class="wrapbox">
             <!-- 上拉加载 -->
            <mt-loadmore       
                :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                :auto-fill="false" ref="loadmore">
			<ul class="list list-xm">
				<li v-for="item in proList"  @click="goGoodsDetail(item.id)">
					<h2 class="title">{{item.goods_name}}</h2>
					<p class="desc">{{item.intro}}</p>
					<p class="price"><span class="price-now">{{item.price}}.00</span><span class="price-old">{{item.old_price}}.00</span></p>
					<div class="tagbox">
                        <i class="tag tag-tehui" v-if="item.is_buy_now">特惠</i><i class="tag tag-baoxian" v-if="item.is_special_offer">推荐</i>
                    </div>
					<div class="imgbox">
                        <img :src="imgUrl+item.image_url"><!-- <i class="mark mark-guanfang">官方</i> -->
                    </div>
					<button class="btn-yuyue">预 约</button>
				</li>
			</ul>
			<div slot="bottom" class="mint-loadmore-bottom">
                <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
				</div>
            </mt-loadmore>
		</div>
  </div>
</template>
<script>
import searchTitle from '../common/searchTitle'
import { Toast } from 'mint-ui'
export default {
    name:'goods_list',
    components:{
        searchTitle
    },
    data () {
        return {
            getData:{
                order:0,
                pageIndex:0,
                pageSize:5,
                query:'',
                type:''
            },
            pageTitle:'美容项目',
            proList:[],
            allLoaded: false,   //数据是否加载完毕
            bottomStatus: '',   //底部上拉加载状态
            popupVisible: false
        }
    },
    mounted(){
        this.loadData()
    },
    methods: {
        goGoodsDetail(id){
			this.$router.push('/goods_detail?id='+id);
	    },
        loadData(){
            if (this.$route.query.keyWords) {
                this.getData.query = this.$route.query.keyWords;
                this.pageTitle = '美容项目搜索';
            }
            else if(this.$route.query.type)
            {
                this.getData.type = 'hot';
                this.pageTitle = '美容热点';
            }
            this.getData.pageIndex++
       
            this.$http.get('/Goods?order='+this.getData.order+'&index='+this.getData.pageIndex+'&size='+this.getData.pageSize+'&query='+this.getData.query+'&type='+this.getData.type)
            .then(res=>{
            res.data.data.forEach(element => {
                    this.proList.push(element)
                });
                if (res.data.data.length==0) {
                    this.allLoaded = true				// 停止上拉
                    Toast({message:'没有更多内容了！',position: 'bottom',duration: 3000})
                }
            })
            .catch(err=>{
                this.$router.push('/error');
            })
        },
       loadBottom(){
           this.loadData();
       },
    }
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>


