<template>
  <div>
      <!-- <col-title></col-title> -->
	  <header class="header-bar">
        <a class="header-icon header-icon-back pull-left" @click="$router.back(-2)"></a>
        <a class="header-title">用户登录</a>
        <a class="header-icon header-icon-more pull-right"></a>
    </header>
      		<div class="wrapbox padding-footer">
			<div id="item1" class="withdraw-operate">
				<div class="padding20">
					<table class="table-default">
						<tr>
							<th>用户名：</th>
							<td><input type="text" placeholder="请输入用户姓名" v-model="model.name"></td>
						</tr>
						<tr>
							<th>密码：</th>
							<td><input type="password" placeholder="请输入密码"
							v-model="model.pwd"></td>
						</tr>
					</table>
					<div class="go-reg"><a href="#" @click="goReg">还没账号？</a></div>					
				</div>
				<div class="box-space-top15">
					<div class="sys-service padding20">
						<h1 class="txt-assistant">温馨提示</h1>
						<p>为确保您账户的安全及正常使用，依《网络安全法》相关要求，6月1日起会员账户需绑定手机。如您还未绑定，请尽快完成，感谢您的理解及支持！</p>
					</div>
				</div>
			</div>
		</div>
		<footer class="footer-bar">
			<div class="padding20-x">
				<button class="btn-lg btn-ok" @click="userlogin">确认登录</button>
			</div>
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
export default {
	name:'login',
	components: {
		colTitle
	},
	data () {
		return {
			model:
			{
				name: "",
				pwd: ""
			},
			return_url:""
		}
	},
	mounted(){
		if(localStorage.getItem('return_url'))
		{
			this.return_url = localStorage.getItem('return_url');
			localStorage.removeItem('return_url');
			
			Toast({
					message: '请先登录！',
					position: 'top',
					duration: 2000
			});
		}
		console.log(this.return_url)
	},
	methods: {
		checkFrom(){
			// 表单验证
			if (this.userName=='') {
				Toast({
					message: '用户名不能为空',
					position: 'top',
					duration: 2000
				});
				return false
			}
			if (this.passWord=='') {
				Toast({
					message: '密码不能为空',
					position: 'top',
					duration: 2000
				});
				return false
			}
		},
		userlogin(){
			this.checkFrom()
			this.$http.post('/User/Login',this.model)//this.$qs.stringify(this.model))
			.then(res=>{
				Toast({
					message: '登录成功！',
					position: 'center',
					duration: 3000
				});
				localStorage.setItem("tokenId",res.data.token)
				if(this.return_url)
					this.$router.push(this.return_url);
				else
					this.$router.push('/main')
			})
			.catch(err=>{
				Toast({
					message: '登录失败，用户名或密码错误！',
					position: 'center',
					duration: 3000
				});
			})

		
		},
		goReg(){
			this.$router.push('/register')
		}
	}
}
</script>
<style scoped>
.table-default th{text-align: right;padding-right: 15px}
.go-reg{padding-top: 10px;text-align: right}
.go-reg a{text-decoration: none;color: #adadad}
</style>


