<template>
  <div>
   		<col-title>{{data.shop_name}}</col-title>
			<div class="wrapbox">
			<!--幻灯片-->
            <carousel :bannerList='this.data.banner_list'></carousel>
			<ul class="list list-jg" style="padding: 0;">
				<li>
					<h2 class="title">{{data.shop_name}}</h2>
					<p class="qualification">资质：{{data.aptitude}}</p>
					<p class="address">地址：{{data.address}}</p>					
					<div class="btnbox"><button class="btn-icon btn-tel">电话咨询</button><button class="btn-icon btn-favorate" @click="save_favor()">加入收藏</button></div>
					<div class="imgbox"><img :src="data.logo?imgUrl+data.logo:no_img"><i class="mark mark-renzheng" v-if="data.is_verify">已认证</i></div>
				</li>
			</ul>			
			<div class="box-space-top15">
				<!--机构项目推荐-->
				<div class="padding20-x">
					<h3 class="title-col"><b>推荐项目</b><router-link to="/shop_goods" class="pull-right">更多</router-link></h3>
				</div>			
				<ul class="list list-xm">
					<li v-for="item in data.goods_list" @click="goGoodsDetail(item.id)">
						<h2 class="title">{{item.goods_name}}</h2>
						<p class="desc">{{item.intro}}</p>
						<p class="price"><span class="price-now">{{item.old_price.toFixed(2)}}</span><span class="price-old">{{item.price.toFixed(2)}}</span></p>
						<div class="tagbox"><i class="tag tag-tehui" v-if="item.is_buy_now">特惠</i><i class="tag tag-baoxian" v-if="item.is_special_offer">推荐</i></div>
						<div class="imgbox"><img :src="imgUrl+item.image_url"></div>
						<button class="btn-yuyue" >详 情</button>
					</li>
				</ul>
				<!--推荐技师-->
				<div class="padding20-x">
					<h3 class="title-col"><b>推荐技师</b><router-link to="/shop_beautician" class="pull-right">更多</router-link></h3>
				</div>
				<ul class="list list-zj">
					<li v-for="item in data.beautician_list" @click="goBeauticianDetail(item.id)">
						<h2 class="title">{{item.beautician_name}} <span class="txt-gray">{{item.duties}}</span></h2>
						<p class="workplace">{{item.show_name}}</p>
						<div class="countbox">
							<p class="total">已预约：<b class="txt-important">0000</b></p>
							<p class="booktag">
			                	<span class="tag-num tag-num-strong"><i class="tag-num-txt">{{item.keyword}}</i></span>
		           	   		</p>						
						</div>
						<div class="imgbox"><img :src="imgUrl+item.image_url"><i class="mark mark-renzheng" v-if="item.is_verify">已认证</i></div>
					</li>
				</ul>
			</div>
		</div> 
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import Carousel from '../common/carousel'
import { Toast } from 'mint-ui'
export default {
  name:'shop_detail',
  components: {
	  colTitle,
	  Carousel
    },
    data () {
		return {
			shop_id:this.$route.query.id,
			tokenId:localStorage.getItem("tokenId"),
			data:[]
		}
	},
	mounted(){
		this.getMainData()
	},
	methods: {
		getMainData(){
			this.$http.get('/Shop/'+this.shop_id).then(res=>{
				this.data = res.data;
				this.data.banner_list = JSON.parse(this.data.banner_list);
			})
			.catch(err=>{
				this.$router.push('/error')
			})

			this.$http.get('/Shop/'+this.shop_id).then(res=>{
				this.data = res.data;
				this.data.banner_list = JSON.parse(this.data.banner_list);
			})
			.catch(err=>{
				this.$router.push('/error')
			})
		},
		goGoodsDetail(id){
			this.$router.push('/goods_detail?id='+id);
		},
		goBeauticianDetail(id){
			this.$router.push('/beautician_detail?id='+id);
		},
		save_favor()
		{
			if(!this.tokenId)
			{
				Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'top',
						duration: 2000
				});
				return ;
			}

			this.$http.post('/UserFavor',{
					type: 2,
					itemid: this.shop_id
			},{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{
				if(res.data.status=="saved")
				{
					Toast({
							message: '收藏成功',
							position: 'bottom',
							duration: 2000
					});
				}
				else
				{
					Toast({
							message: '已取消收藏',
							position: 'bottom',
							duration: 2000
					});
				}
			})
			.catch(err=>{

				if(err.response.status==401)
				{
					Toast({
						message: '您尚未登录，登录后可使用收藏',
						position: 'bottom',
						duration: 2000
					});
				}
			})
			
		}
	}
}
</script>



