<template>
  <div>
      <search-title>美容/整形机构</search-title>
      		<div class="wrapbox">
			<div class="box-space-top15">
                    <!-- 上拉加载 -->
                    <mt-loadmore       
                    :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
                    :auto-fill="false" ref="loadmore">
				<ul class="list list-jg">
					<li v-for="shop in shopList" @click="goShopDetail(shop.id)">
						<h2 class="title">{{shop.shop_name}}</h2>
						<p class="qualification">资质：{{shop.aptitude}}</p>
						<p class="address">地址：{{shop.address}}</p>
						<div class="countbox">
							<p class="total">已预约：<b class="txt-important">{{shop.sale_count}}</b></p>
						</div>
						<div class="imgbox"><img :src="imgUrl+shop.logo"><i class="mark mark-renzheng" v-if="shop.is_verify
">已认证</i></div>
					</li>
				</ul>	
				<div slot="bottom" class="mint-loadmore-bottom">
					<span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
				</div>
				</mt-loadmore>
			</div>
		</div>
  </div>
</template>
<script>
import searchTitle from '../common/searchTitle'
import { Toast } from 'mint-ui';
export default {
  name:'shop_list',
   components:{
      searchTitle
    },
    data () {
        return {
			query:"",
			pageIndex:0,
            pageSize:5,
			allLoaded: false,   //数据是否加载完毕
            bottomStatus: '',   //底部上拉加载状态
			shopList:[]
        }
    },
    mounted(){
		if(this.$route.query.keyWords)
			this.query = this.$route.query.keyWords;

		this.loadData()
    },
    methods: {
        loadBottom(){
			this.loadData();
		},
		loadData()
		{
			this.pageIndex++
			// 上拉加载更多……
            this.$http.get('/Shop?order=1&index='+this.pageIndex+'&size='+this.pageSize+'&query='+this.query+'&verify=')
            .then(res=>{
				res.data.data.forEach(element => {
					this.shopList.push(element)
				});

                if (res.data.data.length==0) {
                    this.allLoaded = true				// 停止上拉
                    Toast({message:'没有更多内容了！',position: 'bottom',duration: 3000})
                }
            })
            .catch(err=>{
                this.$router.push('/error')
            })
		},
		goShopDetail(id){
			this.$router.push('/shop_detail?id='+id);
		}
    }
}
</script>
<style scoped>
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
.orderBy{line-height: 40px}
.orderBy li{border-bottom: 1px solid #efefef}
</style>


