<template>
  <div>
      <col-title>订单详情</col-title>
					<div class="wrapbox padding-footer">
			<!--项目-->
			<ul class="list list-xm book-xm">
				<li>
					<h2 class="title">{{data.goods_name}}</h2>
					<p class="price">订购数量：{{data.quantity}}</p>
					<p class="price">总额：<span class="price-now">{{data.total}}</span></p>
					<div class="imgbox"><img :src="data.image_url?imgUrl+data.image_url:no_img"><i class="mark mark-guanfang">官方</i></div>
				</li>
			</ul>		
			<!--订单信息-->
			<div class="padding20-x">
				<table class="table-default">
					<tr>
						<th>订单号：</th>
						<td colspan="3">{{data.serial_no}}</td>
					</tr>
					<tr>
						<th>订单状态：</th>
						<td colspan="3">{{data.status_name}}</td>
					</tr>
				</table>
			</div>
			<div class="box-space-top15">
				<div class="padding20-x">
					<table class="table-default">
						<tr>
							<th>预约人：</th>
							<td>{{data.user_name}}</td>
						</tr>
						<tr>
							<th>联系电话：</th>
							<td>{{data.tel}}</td>
						</tr>
						<tr>
							<th>服务时间：</th>
							<td>{{data.start_time}}</td>
						</tr>
						<tr>
							<th>门店地址：</th>
							<td>{{data.address}}</td>						
						</tr>						
						<tr>
							<th>操作技师：</th>
							<td>{{data.beautician_name}}</td>						
						</tr>
					</table>
					<div class="order-cancel" v-if="data.status_id<2" @click="cancelOrder()"><a>申请取消订单</a></div>
				</div>
			</div>
		</div>
		<footer class="footer-bar footer-djq">
			<div class="padding20-x">
				<button class="btn-lg btn-ok" v-if="data.status_id==1" @click="goComfirmOrder()">去预约</button>
				<button class="btn-lg btn-ok" v-if="data.status_id==2" @click="finishOrder()">完成预约</button>
			</div>
		</footer>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { MessageBox } from 'mint-ui'
import { Toast } from 'mint-ui'
export default {
  name:'order_detail',
  components: {
			colTitle
  },
  data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			serial_no:this.$route.query.id,
			data:[]
	}
	},
	mounted(){
		if(this.tokenId)
            this.getMainData()
		else
			this.goLogin();
	},
	methods: {
		getMainData(){
			this.$http.get('/Order?serial_no='+this.serial_no,{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
				this.data = res.data;
			})
			.catch(err=>{
				if(err.response.status==401)
					this.goLogin();
				else
					this.$router.push('/error');
			})
		},
		goComfirmOrder()
		{
			//去订单支付页面
        	this.$router.push({path: 'order_buy', query:{serial_no: this.serial_no}})
			console.log('去订单支付页面');
		},
		finishOrder(){
			MessageBox.confirm("确定执行此操作吗?").then(action => {
                if (action) {
                    Toast({
                    message: '支付完成，您的款项将发送至商家！',
                    position: 'center',
                    duration: 5000
                    });
					//确认按钮
					this.$http.post('/OrderComfirm',{
						serial_no:this.serial_no,
						status:3
					},{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
						this.data.status_id=3;
						this.data.status_name = '已完成';
					})
					.catch(err=>{
						this.$router.push('/error')
					})
                }
            });
		},
		cancelOrder()
		{
			MessageBox.confirm("您确定要取消此订单吗?").then(action => {
                if (action) {
                    Toast({
                    message: '订单已取消',
                    position: 'center',
                    duration: 5000
                    });
				
					//确认按钮
					this.$http.post('/OrderComfirm',{
						serial_no:this.serial_no,
						status:4
					}).then(res=>{
						this.data.status_id=4;
						this.data.status_name = '已取消';
					})
					.catch(err=>{
						if(err.response.status==401)
							this.goLogin();
						else
							this.$router.push('/error');
					})
                }
            });
		},
        goLogin()
        {
            localStorage.setItem("return_url","/order_detail?id="+this.serial_no);
            this.$router.push('/Login')
        }
	}
}
</script>



