<template>
    <div>
        <col-title>确认余额支付</col-title>
            <form method="post" action="" id="form1">
        <div class="aspNetHidden">
            <input type="hidden" name="" id="" value="" />
        </div>
        <header class="header-bar">
            <a class="header-icon header-icon-back pull-left" <a class="header-icon header-icon-back pull-left" href='#'></a>
        <a class="header-title">确认余额支付</a>
        <a class="header-icon header-icon-more pull-right"></a>
        </header>

        <div class="wrapbox">
            <!--确认支付-->
            <div class="viewport">
                <div class="qianbao">
                    <div class="pay_confirm zhifu">
                        <p class="label-zf">订单金额</p>
                        <h1 class="jiner"><span id="sp_totalfee">{{data.total}}</span></h1>
                    </div>
                </div>
                <div class="padding20-x btnAreaTx">
                    <input type="button" @click="pay()" name="Button1" value="立即支付" id="Button1" class="btn-lg btn-ok txBtn" />
                </div>
            </div>
        </div>
    </form>
    </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { Toast } from 'mint-ui'
export default {
    name:'accpay',
    components: {
	  colTitle
    },
    data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			serial_no:this.$route.query.serial_no,
			data:[]
	    }
    },
	mounted(){
		if(this.tokenId)
            this.getMainData()
        else
        {
			localStorage.setItem("return_url","/accpay?serial_no="+this.serial_no);
			this.$router.push('/Login')
		}
	},
	methods: {
		getMainData(){
            var that = this;
            this.$http.get('/Order?serial_no='+this.serial_no,{headers: {"authorization": "Bearer " + this.tokenId}})
                .then(res=>{
                    this.data = res.data;
                   
                    if(res.data.status_id==1)
                        return ;
                    var msg = "";
                    if(res.data.status_id==2)
                        msg = "订单已经支付,请勿重复支付";
                    if(res.data.status_id==3)
                        msg = "订单已经完成,请勿重复支付";
                    if(res.data.status_id==4)
                        msg = "订单已经取消,无法再次支付";
                    Toast({
                        message: msg,
                        position: 'center',
                        duration: 2000
                    });

                    setTimeout(() => {
                        that.$router.push('/order_list')
                    }, 2000);
                })
                .catch(err=>{
                    this.$router.push('/error')
                })
        },
        pay(){
            this.$http.post('/OrderComfirm',{
                serial_no:this.serial_no,
                status:2
            },{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
                this.$router.push('/success_pay');
            })
            .catch(err=>{
                this.$router.push('/error_pay');
            })
        }
    }
}
</script>
<style scoped>
.pay_confirm, .pay_success{
	background: url(../../assets/img/order-confirm.svg) no-repeat center top 0.5rem / 2rem;
	text-align: center;
	padding-top: 2.8rem;
	color: #333333;
	margin-bottom: 1rem;
}
.pay_confirm p, .pay_success p {
	font-size: 0.375rem;
}
.pay_confirm h1, .pay_success h1{
	font-size: 1rem;
	margin-top: 0.5rem;
}
.pay_confirm h1::before, .pay_success p b::before{
	content: "￥";
}
</style>



