import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import qs from 'qs';
Vue.prototype.$qs = qs;

Vue.use(MintUI)
Vue.prototype.$http = axios
//axios.defaults.baseURL = 'http://film.glkjjt.com/api'
axios.defaults.baseURL = 'http://localhost:56523/api' // 默认服务端请求地址
//axios.defaults.baseURL = 'http://localhost:56523/api'
Vue.prototype.imgUrl = "http://www.erailway.cn/" 
Vue.prototype.no_img = "../../assets/img/no-img.png"
Vue.filter('subTime', function (value) {
  return value?(value+"").substr(0,10):"";
})

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



