<template>
  <div>
     <col-title>确认购买</col-title>
     		<div class="wrapbox padding-footer">
			<!--项目-->
			<ul class="list list-xm book-xm">
				<li>
					<h2 class="title">{{data.goods_name}}</h2>
					<p class="price">单价：<span class="price-now">{{data.price}}.00</span></p>
					<div class="num-count clearfix">
						<span class="num-txt">选择数量：</span>
						<span class="num-control">
							<a class="num-less" @click="submitData.quantity<=1?submitData.quantity==1:submitData.quantity--"></a>
							<input type="text" class="num-input" v-model="submitData.quantity">
							<a class="num-add" @click="submitData.quantity++"></a>
						</span>
					</div>
					<div class="imgbox"><img :src="data.image_url?imgUrl+data.image_url:no_img"><i class="mark mark-guanfang">官方</i></div>
				</li>
			</ul>
			<!--订单信息-->
			<div class="padding20-x">
				<table class="table-default">
					<tr>
						<th>门店地址：</th>
						<td>
							<textarea placeholder="门店地址" v-model="data.address" rows="2" warp="soft">{{data.address}}</textarea>
						</td>
					</tr>
					<tr >
						<th>美容师：</th>
						<td>{{data.beautician_name}}<a class="link pull-right" @click="chooseBeautician">更换美容师</a></td>
					</tr>
					<tr>
						<th>服务时间：</th>
						<td>{{submitData.start_time}}<a class="link pull-right" @click="chooseTime">更换时间</a></td>
					</tr>
					<tr>
						<th>预约人：</th>
						<td><input type="text" placeholder="用户收件姓名" v-model="submitData.user_name"></td>
					</tr>
					<tr>
						<th>联系电话：</th>
						<td><input type="text" placeholder="用户电话" v-model="submitData.tel"></td>
					</tr>
				</table>
			</div>
		</div>
		<footer class="footer-bar footer-xm">
			<div class="price pull-left">
				<h3>总价：<strong>￥{{totalPrice}}.00</strong></h3>
			</div>
			<span class="btn-yuyue btn-book-yuyue pull-right" @click="addOrder()"><button>确定预约</button></span>
		</footer>
		<!-- 选择美容师 -->
		<mt-popup v-model="showChoose" position="right">
			<chooseB ref="test"  @hideBeautician="onHideBeautician"></chooseB>
		</mt-popup>
		<!-- 选择时间 -->
		<mt-datetime-picker ref="picker" setSlotValue="" type="datetime" v-model="startTime"  @confirm="chooseGetTime" :startDate="startDate" :endDate="endDate"></mt-datetime-picker>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import chooseB from '../pages/choose_beautician'
import { Popup } from 'mint-ui'
import { DatetimePicker } from 'mint-ui'
export default {
	name:'order_buy',
	components: {
		colTitle,
		chooseB
	},
	data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			data:{
				goods_name:"",
				price:0,
				image_url:'',
				address:'',
				beautician_name:'',
			},
			submitData:{
				serial_no:"",
				beautician_id:0,
				goods_id:0,
				quantity:1,
				start_time:'2018/6/2',
				tel:'',
				user_name:''
			},
			
			showChoose:false,
			startTime:'',
			startDate:new Date(),
			endDate:new Date(Date.parse(new Date) + 1000 * 60 * 60 * 24 * 7 * 59)
		}
	},
	mounted () {
		if(this.tokenId)
			this.checkArguments()
		else
			this.goLogin();
	},
  	computed: {
	  	totalPrice(){
		  	return this.submitData.quantity * this.data.price
	  	}
  	},
  	methods: {
		onHideBeautician (id,name) {
			//关闭窗口
			this.showChoose = false;
			//显示美容师信息
			this.submitData.beautician_id = id;
			this.data.beautician_name = name;
		},
	  	checkArguments(){
			if (this.$route.query.id) {
				this.$http.get('/Goods/'+this.$route.query.id)
				.then(res=>{
					this.data.goods_name = res.data.goods_name;
					this.data.price = res.data.price;
					this.data.image_url = res.data.image_url;
					this.data.address = res.data.shop_address;
					//提交信息
					this.submitData.goods_id = res.data.id;
				})
				.catch(err=>{
					if(err.response.status==401)
						this.goLogin();
					else
						this.$router.push('/error');
				})
			}
			else if(this.$route.query.serial_no){
				this.$http.get('/Order?serial_no='+this.$route.query.serial_no,
				{headers: {"authorization": "Bearer " + this.tokenId}})
				.then(res=>{
					this.data.goods_name = res.data.goods_name;
					this.data.price = res.data.price;
					this.data.image_url = res.data.image_url;
					this.data.quantity = res.data.quantity;
					this.data.address = res.data.address;
					this.data.beautician_name = res.data.beautician_name;
					
					this.submitData.serial_no = res.data.serial_no;
					this.submitData.start_time= res.data.start_time;
					this.submitData.tel= res.data.tel;
					this.submitData.user_name = res.data.user_name;
					this.submitData.beautician_id= res.data.beautician_id;
					this.submitData.goods_id = res.data.goods_id;

				})
				.catch(err=>{
					if(err.response.status==401)
						this.goLogin();
					else
						this.$router.push('/error');
				})
			}
			else{
				console.log("全部没有跳转")
			}
		},
		chooseBeautician(){
			//触发子窗口加载美容师事件
			this.$refs.test.$emit('showBeautician',this.submitData.goods_id);
			//弹出窗口
			this.showChoose = true
		},
		// 选择时间
		chooseTime(){
			this.$refs.picker.open();
		},
		chooseGetTime(){
			var d = new Date(this.startTime);  
			this.submitData.start_time = 
			d.getFullYear() + '/' + 
			((d.getMonth() + 1)<10?'0'+(d.getMonth() + 1):(d.getMonth() + 1)) + '/' + 
			(d.getDate()<10?'0'+d.getDate():d.getDate())+ ' ' + 
			(d.getHours()<10?'0'+d.getHours():d.getHours()) + ':' + 
			(d.getMinutes()<10?'0'+d.getMinutes():d.getMinutes());
		},
		addOrder()
		{
			if (this.$route.query.id) {
				//通过商品编号进入本页面，下新订单
				this.$http.post('/Order',this.submitData,{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
					this.$router.push('/accpay?serial_no='+res.data.SerialNo);
				})
				.catch(err=>{
					if(err.response.status==401)
						this.goLogin();
					else
						this.$router.push('/error');
				})
			}else
			{
				//通过订单编号进入本页，修改订单
				this.$http.put('/Order',this.submitData,{headers: {"authorization": "Bearer " + this.tokenId}}).then(res=>{
					this.$router.push('/accpay?serial_no='+this.submitData.serial_no);
				})
				.catch(err=>{
					 if(err.response.status==401)
						this.goLogin();
					else
						this.$router.push('/error');
				})
			}
		},
		goLogin()
		{
			if (this.$route.query.id) {
				localStorage.setItem("return_url","/order_buy?id="+this.$route.query.id);
			}
			else if(this.$route.query.serial_no){
				localStorage.setItem("return_url","/order_buy?serial_no="+this.$route.query.serial_no);
			}
            this.$router.push('/Login')
		}
  	}
}
</script>
<style scoped>
.table-default textarea{min-height:0;border: none}
.mint-popup-right{width: 100%;height: 100%}
</style>



