<template>
  <div>
      <col-title>资料设置</col-title>
		<div class="wrapbox padding-footer">
			<div class="user-infoset">
				<div class="padding20">
					<table class="table-default">
						<tr>
							<th>账号：</th>
							<td><input type="text" class="in-txt"  v-model="data.user_name" disabled></td>
						</tr>
						<tr>
							<th>昵称：</th>
							<td><input type="text" class="in-txt" v-model="data.nick_name" placeholder="请输入昵称"></td>
						</tr>
						<tr>
							<th>手机号：</th>
							<td><input type="text" class="in-txt" v-model="data.tel"  placeholder="请输入手机号"></td>
						</tr>
						<tr>
							<th>QQ：</th>
							<td><input type="text" class="in-txt" v-model="data.qq"  placeholder="请输入QQ号"></td>
						</tr>
						<tr>
							<th>电子邮箱：</th>
							<td><input type="text" class="in-txt" v-model="data.email"  placeholder="请输入邮箱"></td>
						</tr>
					</table>					
				</div>
				<div class="box-space-top15">
					<div class="padding20">
						<table class="table-default">
							<tr>
								<th>性别：</th>
								<td>
									<input type="radio" id="radio-1-1" name="radio-1" value="男" class="regular-checkbox" disabled/><label for="radio-1-1"><i></i>男士</label>
									<input type="radio" id="radio-1-2" name="radio-1" value="女" class="regular-checkbox"  checked="checked" disabled /><label for="radio-1-2"><i></i>女士</label>
								</td>
							</tr>
							<tr>
								<th>生日：</th>
								<td><input type="datetime" class="in-txt" v-model="data.birthday" placeholder="请选择出生日期" ></td>
							</tr>
							<tr>
							<th>身份证：</th>
								<td><input type="text" class="in-txt" v-model="data.sno" placeholder="请输入有效身份证"></td>
							</tr>
						</table>					
					</div>
				</div>
			</div>
		</div>
		<footer class="footer-bar">
			<div class="padding20-x">
				<button class="btn-lg btn-ok" @click="submit">确认修改</button>
			</div>
		</footer>

  </div>
</template>
<script>
import colTitle from '../common/colTitle'
import { DatetimePicker } from 'mint-ui'
import { Toast } from 'mint-ui'
export default {
    name:'register',
    components: {
      	colTitle
    },
	data () {
		return {
			tokenId:localStorage.getItem("tokenId"),
			data:[]
		}
	},
	mounted(){
		if(this.tokenId)
            this.getData()
		else
			this.goLogin();
	},
	methods: {
		getData(){
			this.$http.get('/User',{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{
				this.data = res.data;
				if(res.data.birthday)
					console.log(res.data.birthday)
			})
			.catch(err=>{
				if(err.response.status==401)
                    this.goLogin();
                else
                  this.$router.push('/error');
			})
		},
		submit(){
			this.$http.put('/User',{
					nick_name:this.data.nick_name,
					email:this.data.email,
					sno:this.data.sno,
					qq:this.data.qq,
					tel:this.data.tel,
					birthday: this.data.birthday
				},{headers: {"authorization": "Bearer " + this.tokenId}})
			.then(res=>{
				Toast({
                    message: '用户资料修改成功！',
                    position: 'center',
                    duration: 2000
				});
				setTimeout(() => {
                    this.$router.push('/user')
                }, 2000);
			})
			.catch(err=>{
				if(err.response.status==401)
                    this.goLogin();
                else
                   this.$router.push('/error');
			})
		},
		goLogin()
        {
            localStorage.setItem("return_url","/user_modify");
            this.$router.push('/Login')
        }
	}
}
</script>
<style scoped>
input[type="text"]:disabled{background:transparent}
</style>



