<template>
    <!--幻灯片-->
    <div class="slider slider-index">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,index) in bannerList" :key='index'><img :src="imgUrl+item.image_url
"></mt-swipe-item>
	    </mt-swipe>
    </div>
</template>
<script>
export default {
  name:'carousel',
  props: ['bannerList']
}
</script>
<style scoped>
.slider-index img{width: 100%}
</style>




