<template>
    <div>
        <header class="header-bar index-headerbar">
			<router-link to="/" class="index-headerbar-logo"></router-link>
			<a class="index-headerbar-search"><input type="text" placeholder="发现美好，搜一下咯~" id="searchBox" v-on:click.capture="jumpSearch"></a>
			<a class="header-icon pull-right" :class="userIcon?'user-icon2':'user-icon1'" @click="goUser"></a>
		</header>
		<div class="wrapbox padding-footer">
			<!-- 轮播图组件111111 -->
            <carousel :bannerList='bannerList'></carousel>
			<!--图标导航-->
			<div class="index-iconnav">
				<router-link to="/goods_list" class="iconnav-mrxm">美容项目</router-link>
				<router-link to="/shop_list" class="iconnav-mrjg">美容机构</router-link>	
				<router-link to="/goods_list?type=hot" class="iconnav-mrrd">美容热点</router-link>	
				<router-link to="/beautician_list" class="iconnav-jstj">技师推荐</router-link>
			</div>
			<div class="index-iconnav">	
				<a class="iconnav-zxxm">整形项目</a>
				<a class="iconnav-zxjg">整形机构</a>
				<a class="iconnav-zxrd">整形热点</a>
				<a class="iconnav-ystj">医师推荐</a>			
			</div>
						<!--频道展示-->
			<div class="box-space-top15 margin-top20 indexmol indexmol-mr">
				<div class="indexmol-title"><span>美容精选</span></div>
				<dl class="indexmol-nav">
					<dt>
						<a :href="item.url" v-for="item in hairdressingNav">{{item.search_key}}</a>
					</dt>
					<dd><a>更多</a></dd>
				</dl>
				<div class="index-ad clearfix">
					<div :class="'ad-0' + (index + 1)" v-for="(item,index) in hairdressing"><a><img :src="imgUrl+item.image_url"></a></div>			
				</div>
			</div>
			<div class="box-space-top15 margin-top20 indexmol indexmol-zx">
				<div class="indexmol-title"><span>整形推荐</span></div>
				<dl class="indexmol-nav">
					<dt>
						<a :href="item.url" v-for="item in plasticTitleNav">{{item.search_key}}</a>
					</dt>
					<dd><a>更多</a></dd>
				</dl>
				<div class="index-ad clearfix">
					<div :class="'ad-0' + (index + 1)" v-for="(item,index) in playYm"><a><img :src="imgUrl+item.image_url"></a></div>		
				</div>
			</div>
			<div class="box-space-top15 margin-top20 indexmol indexmol-wz">
				<div class="indexmol-title"><span>玩赚要美</span></div>
				<dl class="indexmol-nav">
					<dt>
						<a :href="item.url" v-for="item in playYmTitleNav">{{item.search_key}}</a>
					</dt>
					<dd><a>更多</a></dd>
				</dl>
				<div class="index-ad clearfix">
					<div :class="'ad-0' + (index + 1)" v-for="(item,index) in plastic"><a><img :src="imgUrl+item.image_url"></a></div>			
				</div>
			</div>
			<div class="box-space-top15 margin-top20 indexmol indexmol-gz">
				<mt-loadmore       
				:bottom-method="loadBottom" :bottom-all-loaded="allLoaded" 
				:auto-fill="false" ref="loadmore">
				<div class="indexmol-title"><span>大家在关注</span></div>
					<ul class="list list-xm">
						<li v-for="item in attentionArr" @click="nav(item.id)">
							<h2 class="title">{{item.goods_name}}</h2>
							<p class="desc">{{item.intro}}</p>
							<p class="datainfo">已售<b class="txt-b">{{item.sale_count}}</b>件 / <b class="txt-i">{{item.sale_count}}</b>人预约</p>
							<p class="price"><span class="price-now">{{item.price}}.00</span><span class="price-old">{{item.old_price}}.00</span></p>
							<div class="tagbox"><i class="tag tag-tehui" v-if="item.is_buy_now">特惠</i><i class="tag tag-baoxian" v-if="item.is_special_offer">推荐</i></div>
							<div class="imgbox"><img :src="imgUrl+item.image_url"><!-- <i class="mark mark-guanfang">官方</i> --></div>
							<button class="btn-yuyue">预 约</button>
						</li>
					</ul>
				<div slot="bottom" class="mint-loadmore-bottom">
					<span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">屏幕上滑，内容继续加载中……</span>
				</div>
				</mt-loadmore>		
			</div>
		</div>
       <FooterBar></FooterBar>
    </div>
</template>
<script>
import FooterBar from '../common/footer'
import Carousel from '../common/carousel'
import { Toast } from 'mint-ui';
    export default{
        name:'mainArea',
        components:{
            FooterBar,
            Carousel
		},
		data () {
			return {
				userIcon:false,
				tokenId:localStorage.getItem("tokenId"),
				bannerList:[],			// banner
				hairdressing:[],		// 美容精选
				plastic:[],				// 整形推荐
				playYm:[],				// 玩转要美
				hairdressingNav:[],		// 美容精选
				plasticTitleNav:[],		// 整形推荐
				playYmTitleNav:[],		// 玩转要美
				attentionArr:[],		// 大家都在关注
				pageSize:5,
				pageIndex:1,
				allLoaded: false,		//数据是否加载完毕
            	bottomStatus: '',		//底部上拉加载状态
			}
		},
		mounted(){
			this.getMainData();
			if(this.tokenId)
				this.userIcon = true
			else
				this.userIcon = false;

		},
		methods: {
			loadBottom(){
				this.loadData();
			},
			loadData()
			{
				this.pageIndex++
				// 上拉加载更多……
				this.$http.get('/Goods?order=1&index='+this.pageIndex+'&size='+this.pageSize+'&query=&type=')
				.then(res=>{
					res.data.data.forEach(element => {
						this.attentionArr.push(element)
					});

					if (res.data.data.length==0) {
						this.allLoaded = true				// 停止上拉
						Toast({message:'没有更多内容了！',position: 'bottom',duration: 3000})
					}
				})
				.catch(err=>{
					this.$router.push('/error')
				})
			},
			getMainData(){
				this.$http.get('/Banner')
				.then(res=>{
					//console.log(res)
					this.bannerList = res.data.bannerList
					this.hairdressing = res.data.adList1
					this.plastic = res.data.adList2
					this.playYm = res.data.adList3
					this.hairdressingNav = res.data.recommandKeyList1
					this.plasticTitleNav = res.data.recommandKeyList2
					this.playYmTitleNav = res.data.recommandKeyList3 
				})
				.catch(err=>{
					console.log(err)
					this.$router.push('/error')
				})

				// 获取大家都在关注数据
				this.$http.get('/Goods?order=1&index=1&size='+this.pageSize+'&query=&type=')
				.then(res=>{
					console.log(res)
					this.attentionArr = res.data.data
				})
				.catch(err=>{
					console.log(err)
					this.$router.push('/error')
				})
			},
			jumpSearch(){
				this.$router.push('/search')
			},nav(id)
			{
				this.$router.push('/goods_detail?id='+id);
			},
			goUser(){
				if(this.tokenId)
					this.$router.push('/user')
				else
					this.$router.push('/login')
			}
		}
    }
</script>
<style scoped>
.index-headerbar-logo{
	display: block;
	width: 30px;
	height: 30px;
	background: url("../../assets/img/mini-logo.png");
	background-size: contain;
	margin: 7px 5% 0 5%
}
.user-icon1{
	display: block;
	width: 30px;
	height: 30px;
	float: right;
	background: url("../../assets/img/user1.png");
	background-size: cover;
	margin: 10px 1% 0 5%
}
.user-icon2{
	display: block;
	width: 30px;
	height: 30px;
	float: right;
	background: url("../../assets/img/user2.png");
	background-size: cover;
	margin: 10px 1% 0 5%
}
.wrapbox{
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	overflow-y: scroll;
	position: absolute;
	z-index: 0;
}
.wrapbox::-webkit-scrollbar{
	display: none;
}
</style>



