import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/pages/main'
import ShopList from '@/components/pages/shop_list'
import BeauticianList from '@/components/pages/beautician_list'
import goodsDetail from '@/components/pages/goods_detail'
import beauticianDetail from '@/components/pages/beautician_detail'
import ShopDetail from '@/components/pages/shop_detail'
import ChooseBeautician from '@/components/pages/choose_beautician'
import OrderBuy from '@/components/pages/order_buy'
import OrderList from '@/components/pages/order_list'
import Search from '@/components/pages/search'
import SearResult from '@/components/pages/searchResult'
import GoodsList from '@/components/pages/goods_list'
import User from '@/components/pages/user'
import UserModify from '@/components/pages/user_modify'
import Userface from '@/components/pages/user_face'
import Register from '@/components/pages/register'
import Login from '@/components/pages/login'
import ErrorTips from '@/components/pages/error'
import OrderDetail  from '@/components/pages/order_detail'
import Favorite from '@/components/pages/favorite'
import Accpay from '@/components/pages/accpay'
import Success from '@/components/pages/success_pay'
import ErrorPay from '@/components/pages/error_pay'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/main'
    },
    {
      path: '/main',
      name: 'mainArea',
      component: Main
    },
    {
      path: '/shop_list',
      name: 'shop_list',
      component: ShopList
    },
     {
      path: '/beautician_list',
      name: 'beautician_list',
      component: BeauticianList
    },
    {
      path: '/goods_detail',
      name: 'goods_detail',
      component: goodsDetail
    },
    {
      path: '/beautician_detail',
      name: 'beautician_detail',
      component: beauticianDetail
     },
     {
      path: '/shop_detail',
      name: 'shop_detail',
      component: ShopDetail
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    {
      path: '/result',
      name: 'result',
      component: SearResult
    },
    {
      path: '/goods_list',
      name: 'goods_list',
      component: GoodsList
    },
    {
       path: '/choose_beautician',
       name: 'choose_beautician',
       component: ChooseBeautician
     },
     {
      path: '/order_detail',
      name: 'order_detail',
      component: OrderDetail
    },
     {
       path: '/order_list',
       name: 'order_list',
       component: OrderList
     },
     {
       path: '/order_buy',
       name: 'order_buy',
       component: OrderBuy
     },
     {
       path: '/user',
       name: 'user',
       component: User
     },
    {
       path: '/user_modify',
       name: 'user_modify',
       component: UserModify
     },
     {
       path: '/user_face',
       name: 'userFace',
       component: Userface
     },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
     {
      path: '/favorite',
      name: 'favorite',
      component: Favorite
    },
    {
      path: '/accpay',
      name: 'accpay',
      component: Accpay
    },
    {
       path: '/success_pay',
       name: 'successPay',
       component: Success
    },
    {
       path: '/error_pay',
       name: 'errorPay',
       component: ErrorPay
     },
    {
      path: '*',
      component: ErrorTips
    },

  ]
})



