<template>
  <div>
    <search-title>搜索</search-title>
    <div class="wrapbox">
		<div class="search-bar">
				<div class="search-selected">
                  <select v-model="selected" @change="changeSelect">
                    <option v-for="item in items" :value="item.typeId">{{item.typeName}}</option>
                </select>
				</div>
				<input type="text" class="search-bar-input" placeholder="搜索项目 / 机构 / 专家~"
                v-model="searchWords"
                @keyup.enter="searchResult"
                >
		</div>
			<div class="empty404">
				<h1>暂无结果……换个关键词试试~</h1>
                <button type="button" @click="$router.push('/search')">返回</button>
			</div>
	</div>
  </div>
</template>
<script>
import searchTitle from '../common/searchTitle'
export default {
  name:'result',
  components: {
      searchTitle
  },
  data () {
      return {
        items:[{typeName:'项目',typeId:1},{typeName:'机构',typeId:2},{typeName:'专家',typeId:3}],
        selected:1,
        searchWords:'',
        localKeysWords:[]
      }
  },
  mounted () {
      // 判断本地存储是否为空
      if (localStorage.getItem('wordTemp')!=null) {
          this.localKeysWords = JSON.parse(localStorage.getItem('wordTemp'))
      }
  },
  methods: {
    changeSelect(){
          // 查询对应类型关键词
          this.getSearchKeys()
    },
         // 获取所有关键词
    getSearchKeys(){
          this.$http.get('/SearchKeys?typeid='+this.selected)
          .then(res=>{
              this.KeysWordsArr = res.data
          })
          .catch(err=>{
              this.$router.push('/error')
          })
    },
    searchResult(e){
        if (e.keyCode === 13) {
            for (let n = 0; n < this.localKeysWords.length; n++) {
                if (this.localKeysWords[n].keywords == this.searchWords ) {
                    this.localKeysWords.splice(n,1)
                }
            }
            var obj = {"id":this.selected,"keywords":this.searchWords}
            this.localKeysWords.unshift(obj)
            localStorage.setItem('wordTemp',JSON.stringify(this.localKeysWords))
            this.$router.push({path: 'goods_list', query:{keyWords: this.searchWords}})
        }
    }
  }
  }
</script>
<style scoped>
.search-selected select{background:#F7F7F7;margin-left: 20px;margin-top: 15px;font-size: 14px;appearance:none;-moz-appearance:none;-webkit-appearance:none;padding-left:5px}
.list-rightarrow li:first-child{width: 100%}
</style>



