<template>
  <div>
    <colTitle></colTitle>
    <div class="wrapbox">
        <div class="empty404">
            <h1>好像出错了...稍后再试吧</h1>
            <button @click="jumpMain">重新加载</button>
        </div>
	</div>
  </div>
</template>
<script>
import colTitle from '../common/colTitle'
export default {
  name:'error',
  components: {
      colTitle
  },
  methods: {
      jumpMain(){
          this.$router.push('/main')
      }
  }
}
</script>




